:root {
	--t-weaker: #d1d1d1;
	--t-weak: #767676;
	--t: #37352f;
	--t-strong: #1d1c1b;
	--t-stronger: #000000;

	--b-weaker: #ffffff;
	--b-weak: #f8f8f7;
	--b: #f2f1ed;
	--b-strong: #e8e7e0;
	--b-stronger: #c1c1c1;

	--a1-weaker: #a7e2bf;
	--a1-weak: #71d099;
	--a1: #3cb46e;
	--a1-strong: #37a465;
	--a1-stronger: #2a7e4d;

	--a1-gradient: linear-gradient(-45deg, #3fa86b, #42c478);
	--a1-gradient-strong: linear-gradient(-45deg, #37a465, #37a465);

	--a1-translucent: #3cb46eaa;
	--a1-translucent-weaker: #3cb46e77;

	--a1-c: #ffffff;

	--shadow: 0px 1px 1px 0px rgba(0, 0, 0, 0.04);

	--elevate-bezier: cubic-bezier(.17, .67, .24, 1.01);
	--elevate-before:
		0 1px 1px rgba(0, 0, 0, 0.05),
		0 2px 2px rgba(0, 0, 0, 0.05), 
		0 4px 4px rgba(0, 0, 0, 0.05), 
		0 8px 8px rgba(0, 0, 0, 0.05),
		0 16px 16px rgba(0, 0, 0, 0.05);

	--elevate-before-filter:
		drop-shadow(0 1px 1px rgba(0, 0, 0, 0.03))
		drop-shadow(0 2px 2px rgba(0, 0, 0, 0.03))
		drop-shadow(0 4px 4px rgba(0, 0, 0, 0.03))
		drop-shadow(0 8px 8px rgba(0, 0, 0, 0.03))
		drop-shadow(0 16px 16px rgba(0, 0, 0, 0.03));

	--elevate-weak:
		0 1px 1px rgba(0, 0, 0, 0.03),
		0 2px 2px rgba(0, 0, 0, 0.03), 
		0 4px 4px rgba(0, 0, 0, 0.03), 
		0 8px 8px rgba(0, 0, 0, 0.03),
		0 16px 16px rgba(0, 0, 0, 0.03);

	--separate:
		1px 0 1px 1px var(--b),
		2px 0 2px 2px var(--b), 
		4px 0 4px 4px var(--b), 
		8px 0 8px 8px var(--b),
		32px 0 32px 32px var(--b),
		64px 0 64px 64px var(--b);

	--elevate-after:
		0 6px 6px rgba(0, 0, 0, 0.05),
		0 7px 7px rgba(0, 0, 0, 0.05), 
		0 9px 9px rgba(0, 0, 0, 0.05), 
		0 13px 13px rgba(0, 0, 0, 0.05),
		0 21px 21px rgba(0, 0, 0, 0.05);

	--neumorphic:
		.5em .5em 1em var(--b-stronger),
		-.5em -.5em 10px var(--b-weaker);

	--link-bg: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='1' height='1'%3E%3Crect x='0' y='.5' width='1' height='.5' fill='rgba(0, 0, 0, .3)'/%3E%3C/svg%3E");

	--link-bg-focus: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='1' height='1'%3E%3Crect x='0' y='.5' width='1' height='.5' fill='rgba(237, 6, 12, .3)' /%3E%3C/svg%3E");
}

body {
	color: var(--t);
	background-color: var(--b);
	overflow-x: hidden;
}

/*! normilio v1.0.0 | BSL-1.0 License | https://github.com/mvoloskov/normilio */
*,
*::before,
*::after {
	box-sizing: border-box;
	line-height: 1.5;
	backface-visibility: hidden;
	text-decoration: inherit;
	vertical-align: inherit;
}

:root {
	font-variant-ligatures: common-ligatures;
	text-rendering: optimizeLegibility;
	font-family:
		-apple-system,
		BlinkMacSystemFont,
		"Segoe UI",
		Roboto,
		Oxygen,
		Ubuntu,
		Cantarell,
		"Fira Sans",
		"Droid Sans",
		"Helvetica Neue",
		sans-serif,
		"Apple Color Emoji",
		"Segoe UI Emoji",
		"Segoe UI Symbol";
}

code,
pre {
	tab-size: 4;
	font-family: monospace;
}

body {
	margin: 0;
}

img {
	display: block;
	height: auto;
}

img,
canvas,
iframe,
video,
svg,
select,
textarea {
	max-width: 100%;
}

[hidden] {
	display: none !important;
}

input {
	font-family: inherit;
}

textarea {
	resize: vertical;
}

[aria-busy="true"] {
	cursor: progress;
}

[disabled],
[aria-disabled="true"] {
	cursor: default;
}

[aria-controls] {
	cursor: pointer;
}

button {
	cursor: pointer;
	font-family: inherit;
}

.butter-loading {
	filter: contrast(0%) brightness(150%) !important;
}

.butter-loading.butter-loaded {
	filter: none !important;
}

